@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Poppins", Arial, Helvetica, sans-serif;
}

body {
  margin: 0;
  background:linear-gradient(155deg, #099ec7, #0b0c0e);
  color: #0b0c0e;
  min-height: 100vh;
  font-size: 1rem;
}

.container {
  margin: 5rem;
  text-align: center;
}


.nav {
  background-color: #0b0c0e;
  color: #099ec7;
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  gap: 2rem;
  padding: 0 1rem;
  font-size: calc(7px + 2vmin);
}

.site-title {
  font-size: 2rem;
  transition: transform 0.3s ease-in-out; 
}

.nav .site-title:hover {
  transform: scale(2.5rem);
}

.site-title span {
  color: aqua;
  font-size: 2.5rem;
}

.nav ul {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  gap: 1rem;
}

.nav a {
  color: inherit;
  text-decoration: none;
  height: 100%;
  display: flex;
  align-items: center;
  padding: .25rem;
  transition: transform 0.3s ease-in-out; 
}

.nav a:hover {
  transform: translateX(10px); 
}

.nav li.active {
  background-color: none;
  
}

.nav li:hover {
  color: #fff;
}


.footer {
  background-color: #0b0c0e;
  color: #099ec7;
  padding: 1rem;
  text-align: center;
  position: absolute; 
  bottom: 0; 
  width: 100%;
  clear: both; 
  
}


.container {
  min-height: 10px;
  padding-bottom: 2rem; 
}